.butReg {
    width: 50px;
    height: 200px;
    background-color: white;
    padding-top: 164px;
    padding-bottom: 20px;
    color: black;
    font-weight: bold;
}

.butReg:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    background-color: rgba(174, 171, 171, 0.5);
    outline:0;
}

.butReg:focus {
    background-color: rgb(94, 93, 93);
    outline:0;
}

.butDiez {
    position: absolute;
    margin-left: -20px;
    width: 40px;
    height: 130px;
    background-color: black;
    color: red;
}

.butDiez:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    background-color: rgba(23, 23, 23, 1);
    outline:0;
}

.butDiez:focus {
    background-color: rgba(23, 23, 23, 1);
    outline:0;
}